:root {
  background-color: #fafafa;

  font-size: 15px;
  color: #333;
}

.App {
  max-width: 800px;
  width: calc(100% - 2rem);
  margin: 100px auto;
}

.con {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.con .box {
  padding: 2rem;
  border: 5px solid #eaeaea;
  border-radius: 4rem;
  text-align: center;
}
.con .box h2 {
  font-size: 0.9em;
  font-weight: bold;
  color: #999;
}
.con .box img {
  width: 80%;
  height: 80%;
  object-fit: cover;
}
.con .box p {
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: -0.03em;
}

.btnCon {
  width: calc(50% - 1rem);
  padding: 1rem;
  text-align: center;
}
.btnCon button {
  padding: 0.5rem 1rem;
  background: #eaeaea;
  border: none;
  border-radius: 1rem;
  margin: 0 3px;
  font-size: 0.8em;
  color: #555;
  cursor: pointer;
  transition: 0.5s;
}
.btnCon button:hover {
  background: #222;
  color: #fff;
}

.con .box.win {
  border-color: #2f8eeb;
}
.con .box.lose {
  border-color: #fff;
}
